  } finally {
    loading.value = false;
  }
};

const formatEmployee = (employee?: NhanVien | string) => {
  if (!employee || typeof employee === 'string') return '';
  return `${employee.ho_dem || ''} ${employee.ten || ''}`.trim();
};

const formatDate = (date?: string) => {
  if (!date) return '';
  return new Date(date).toLocaleDateString('vi-VN');
};

